@import url(https://fonts.cdnfonts.com/css/cubicfive);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  cursor: none;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #000;
  pointer-events: none;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px 0 0 0;
}

.game-name {
  color: #fff;
  font-size: 40px;
  font-family: 'CubicFive10', sans-serif;
  cursor: none;
}
.playfield {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  padding: 16px;
}

.top {
  background-color: #fff;
  width: 100%;
  height: 10px;
}

.middle {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
}

.dotted-line {
  height: calc(100% - 16px);
  border: none;
  border-right: 10px dashed #fff;
  margin-left: -5px;
}

.bottom {
  background-color: #fff;
  width: 100%;
  height: 10px;
}
.bar {
  width: 15px;
  height: 130px;
  background-color: #fff;
}
.bar {
  width: 15px;
  height: 130px;
  background-color: #fff;
}
.ball {
  width: 15px;
  height: 15px;
  background-color: #fff;
  position: absolute;
  left: calc(50% - 10px);
}
.score-container {
  display: flex;
  position: absolute;
  width: calc(100% - 23px);
  justify-content: center;
}

.score {
  margin: 0 50px 0 50px;
}

.score-label {
  font-size: 100px;
  color: #fff;
  font-family: 'CubicFive11', sans-serif;
}
.game-info {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.game-state-label {
  color: #fff;
  font-size: 100px;
  font-family: 'CubicFive10', sans-serif;
  background-color: #000;
}
